
import LoadingImg from '../../public/loadingImg'
import { MessageBox } from 'mint-ui'
import XButton from 'vux/src/components/x-button/index.vue'

import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
name: "mOrder",
data(){
return{
api: this.GLOBAL.baseURL,
orderList:[
]
}
},
components:{
Tab,
TabItem,
XButton,
LoadingImg
},
methods:{
LookMore(){
MessageBox.alert('请前往APP或者客户端查看')
},
lookOrder(){
MessageBox.alert('请前往APP或者客户端查看')
},
goBack(){
this.$router.go(-1)
},
change(index){

if(index <= 1){
this.getOrder(1,index,'','')
}else if(index == 2){
this.getOrder(1,1,1,2)
}else if(index == 3){
this.getRefun(1)
}
},
getRefun(val){
this.$http.post(this.api + '/RefundLUser',{
token: localStorage.getItem('token'),
page:val
})
.then(res=>{
if(res.data.code == 1){

this.orderList = res.data.data.data
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getOrder()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},
getOrder(val,flag,isevaluate,iscomplete){
this.$http.post(this.api + '/orderl',{
token: localStorage.getItem('token'),
page:val,
flag: flag,
isevaluate: isevaluate,
iscomplete: iscomplete
})
.then(res=>{
if(res.data.code == 1){

this.orderList = res.data.data.data
}else if(res.data.code == 3){
this.$http.post(this.api + '/home/index/token')
.then(res=>{
localStorage.setItem('token',res.data.data)
this.getOrder()
})
}else if(res.data.code == 0){
MessageBox.alert(res.data.msg)
}
})
},
getRefund(){
this.$http.post(this.api + '/RefundLUser',{

})
}
},
mounted(){
this.getOrder(1,0,'','')
}
}



